.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 80px;

  @include vp-1439 {
    padding: 0 32px;
  }

  @include vp-1023 {
    padding: 0 24px;
  }

  @include vp-767 {
    padding: 0 16px;
  }
}

.internal {
  .container {
    max-width: 1920px;
    padding: 0;
  }
}
