.help {
  padding: 69px ;
  .container {
    padding: 0;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    background: $color-default-white;
  }

  &__text-wrapper {
    margin-right: 90px;

    .title {
      margin-bottom: 56px;
    }

    .text-25:not(:last-child) {
      margin-bottom: 32px;
    }
  }

  &__btn-wrapper {
    display: flex;
    flex-direction: column;
  }

  &__image {
    min-width: 452px;
    height: 319px;
    margin-bottom: 40px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__btn {
    width: fit-content;
    padding: 32px;
    font-size: 31px;
    font-weight: 400;
    line-height: 1;
    margin: 0 auto;
  }
}
