.selection {
  background-color: $color-romance;
  padding: 80px 0;

  &__heading {
    max-width: 50%;
    margin-bottom: 80px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;
  }

  &__list--first &__item {
    max-width: 149px;
    height: 150px;
    border: 3px solid $color-romance;
    border-radius: 150px;
    width: 150px;
    height: 150px;
    overflow: hidden;
    box-shadow: 0 0 0 2px $color-shamrock-green;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__list--second &__item {
    max-width: 377px;
  }

  &__item {
    .text-31 {
      margin-bottom: 16px;
    }
  }

  &__btn-wrapper {
    display: flex;
    justify-content: center;
  }

  &__btn {
    padding: 24px 32px;
    font-size: 31px;
    font-weight: 400;
    line-height: 1;
  }
}
